import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import LinearProgress from "@material-ui/core/LinearProgress";

import DraggableDialog from "../../components/Dialog";
import Header from "./Header";
import MainFeaturedPost from "./PostWrap/MainFeaturedPost";
import FeaturedPost from "./PostWrap/FeaturedPost";
import DetailPost from "./PostWrap/DetailPost";
import Main from "./Main";
import Footer from "./Footer";
import PostGrid from "./PostWrap/PostGrid";
import enhance from "./enhance";

const useStyles = makeStyles((theme) => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
  totalContentLoadingWrap: {
    height: "100%",
    width: "100%",
    marginTop: "200px",
    marginBottom: "200px",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
  },
  loadingMessage: {
    display: "flex",
    justifyContent: "center",
    fontSize: "20px",
    fontWeight: "bold",
  },
}));

const sections = [
  { title: "CHUYN MN", url: "chuyen-mon-list" },
  { title: "NGOI CHUYN MN", url: "ngoai-chuyen-mon-list" },
];

const Blog = (props) => {
  const classes = useStyles();
  const {
    isAdmin,
    isLoadingPage,
    isOpenDetaiContainer,
    isSubscribeNotifiBot,
    isSuggestSendArticle,
    isNavigateSubmitPageNotifi,
    isShowPaging,
    currentUser,
    currentPageIndex,
    showingPost,
    mainPosts,
    featuredPosts,
    allPost,
    onHandleNavigateAdminPage,
    onHandleSubscribeNotifiByBot,
    onHandleSuggestSendArticle,
    onHandleOpenDetailContainer,
  } = props;

  return (
    <React.Fragment>
      <CssBaseline />
      <Container style={{ width: "90%" }}>
        <Header
          title="HM NAY C G?"
          sections={sections}
          currentUser={currentUser}
          onHandleNavigateAdminPage={onHandleNavigateAdminPage}
          onHandleSubscribeNotifiByBot={onHandleSubscribeNotifiByBot}
          onHandleSuggestSendArticle={onHandleSuggestSendArticle}
        />
        {isLoadingPage && (
          <div className={classes.totalContentLoadingWrap}>
            <span className={classes.loadingMessage}>
              ang ti bi vit. Vui lng i!
            </span>
            <LinearProgress
              color="primary"
              style={{ height: "3px", width: "30%" }}
            />
          </div>
        )}
        {!isLoadingPage && (
          <main>
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                padding: "20px 0",
              }}
            >
              <MainFeaturedPost
                post={mainPosts}
                onHandleOpenDetailContainer={onHandleOpenDetailContainer}
              />
              <div style={{ flexGrow: "2" }}>ANG PHT TRIN</div>
            </div>

            <Grid container spacing={4}>
              <FeaturedPost
                key="featured post"
                post={featuredPosts}
                onHandleOpenDetailContainer={onHandleOpenDetailContainer}
              />
            </Grid>
            <Grid container spacing={5} className={classes.mainGrid}>
              <Main title="Tt c" />
              <PostGrid
                posts={allPost.data}
                totalRecord={Math.ceil(allPost.totalRecord / 3)}
                isShowPaging={isShowPaging}
                currentPageIndex={currentPageIndex}
                onHandleOpenDetailContainer={onHandleOpenDetailContainer}
              />
            </Grid>
          </main>
        )}
        {isOpenDetaiContainer && (
          <DetailPost
            post={showingPost}
            isOpenDetaiContainer={isOpenDetaiContainer}
            onHandleOpenDetailContainer={onHandleOpenDetailContainer}
          />
        )}

        {/*-----------------------------------Nhm thng bo-----------------------------------------------*/}
        {isNavigateSubmitPageNotifi && (
          <DraggableDialog
            dialogContent=" c tri nghim tt nht, vui lng cung cp thng tin cn thit!"
            showTime={100}
          />
        )}
        {isAdmin && (
          <DraggableDialog
            dialogContent="Bn l admin. H thng ang chuyn sang trang qun l bi vit."
            showTime={3000}
          />
        )}
        {isSubscribeNotifiBot && (
          <DraggableDialog
            dialogContent="Tnh nng gi thng bo qua messenger bot ang pht trin."
            showTime={3000}
          />
        )}
        {isSuggestSendArticle && (
          <DraggableDialog
            dialogContent="Tnh nng  ngh gi bi vit ang pht trin.."
            showTime={3000}
          />
        )}
      </Container>
      <Footer description="Mi sao chp ni dung bi vit khng ghi r ngun u vi phm quyn s hu" />
    </React.Fragment>
  );
};

export default enhance(Blog);
